import Layout from '../../layout/'

/**
 * 权限管理模块
 * @type {{path: string, component: *, children: *[]}}
 */
const auth = {
  path: '/auth',
  component: Layout,
  children: [{
    path: 'user',
    name: 'User',
    component: () => import('../../views/auth/user/')
  }, {
    path: 'role',
    name: 'Role',
    component: () => import('../../views/auth/role/')
  }, {
    path: 'menu',
    name: 'Menu',
    component: () => import('../../views/auth/menu/')
  }
  ]
}

export default auth
