// state
const state = {
  toggleActive: true, // toggle按钮是否可用
  collapse: false, // 是否折叠状态，false为未折叠，true为折叠
  breadcrumb: [], // 面包屑，字符串
  defaultRoute: '/', // 默认（当前）激活路由
  currentScreenWidth: '', // 当前屏幕宽度
  asideShow: true // 是否显示侧边栏菜单
}
// getters
const getters = {
  getToggleActive (state) {
    return state.toggleActive
  },
  getCollapse (state) {
    return state.collapse
  },
  getBreadcrumbArr (state) {
    return state.breadcrumb
  },
  getDefaultRoute (state) {
    return state.defaultRoute
  },
  getCurrentScreenWidth (state) {
    return state.currentScreenWidth
  },
  getAsideShow (state) {
    return state.asideShow
  }
}
// mutations
const mutations = {
  UPDATE_TOGGLE_ACTIVE (state) {
    state.toggleActive = !state.toggleActive
  },
  TOGGLE_COLLAPSE (state) {
    // 更改折叠状态
    state.collapse = !state.collapse
  },
  UPDATE_BREADCRUMB (state, val) {
    // 复制数组
    // state.breadcrumb = val.concat()
    // 或
    state.breadcrumb = [...val]
  },
  APPEND_BREADCRUMB (state, val) {
    // 把传进来的子面包屑添加到面包屑数组
    state.breadcrumb.push(val)
  },
  UPDATE_DEFAULT_ROUTE (state, route) {
    state.defaultRoute = route
  },
  UPDATE_CURRENT_SCREEN_WIDTH (state, width) {
    state.currentScreenWidth = width
  },
  UPDATE_ASIDE_SHOW (state) {
    state.asideShow = !state.asideShow
  }
}
// actions
const actions = {
  updateToggleActive (context) {
    context.commit('UPDATE_TOGGLE_ACTIVE') // 更新toggle是否可用
  },
  toggleCollapse (context) {
    // console.log('toggle菜单')
    context.commit('TOGGLE_COLLAPSE') // 伸缩左边菜单
  },
  updateBreadcrumb (context, val) {
    context.commit('UPDATE_BREADCRUMB', val) // 更新面包屑
  },
  appendBreadcrumb (context, val) { // 在原来面包屑的基础上，拼上面包屑
    context.commit('APPEND_BREADCRUMB', val)
  },
  updateDefaultRoute (context, route) {
    context.commit('UPDATE_DEFAULT_ROUTE', route) // 更新默认路由
  },
  updateCurrentScreenWidth (context, width) { // 更新当前屏幕宽度
    context.commit('UPDATE_CURRENT_SCREEN_WIDTH', width)
  },
  updateAsideShow (context) { // 更新侧边栏菜单是否显示
    context.commit('UPDATE_ASIDE_SHOW')
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
