<template>
  <el-scrollbar class="menu-scrollbar">
  <el-drawer
    :visible.sync="drawer"
    direction="ltr"
    :withHeader="false"
    size="260px"
    :before-close="handleClose"
  >
    <!-- 把整个侧边栏菜单引入   -->
    <Aside></Aside>
  </el-drawer>
  </el-scrollbar>
</template>

<script>
import Aside from './index'
import { mapGetters, mapActions } from 'vuex'
import SystemSetting from '../../SystemSetting'

export default {
  name: 'AppMenu',
  components: {
    Aside
  },
  computed: {
    ...mapGetters('ui', {
      collapse: 'getCollapse', // 折叠状态，false为未折叠，true为折叠，映射到this.$store.getters['ui/getCollapse']
      currentScreenWidth: 'getCurrentScreenWidth' // 映射到return this.$store.getters['ui/getCurrentScreenWidth']
    }),
    drawer () { // 是否展示抽屉，如果折叠状态为true是，则不展示false，如果折叠状态为false时，则展示true,并且保证是移动端的前提下
      return !this.collapse && this.currentScreenWidth < SystemSetting.MAX_APP_SCREEN_WIDTH
    }
  },
  methods: {
    ...mapActions('ui', [
      'toggleCollapse'// 更改折叠状态，映射到this.$store.dispatch('ui/toggleCollapse')
    ]),
    handleClose () {
      this.toggleCollapse()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
