<template>
  <div class = "login-container">
    <el-card class="box-card">
      <div>
        <h3 class="login-title">itmacy后台管理系统</h3>
      </div>
      <!--  表单校验几个关键点：model，rule，ref,prop    -->
      <el-form label-width="100px" label-position="top" :model="userForm" :rules="rules" ref="userForm" >
        <el-form-item  prop="username">
          <el-input v-model="userForm.username" placeholder="请输入用户名" clearable></el-input>
        </el-form-item>
        <el-form-item  prop="password">
          <el-input v-model="userForm.password" type="password" placeholder="请输入密码" show-password clearable></el-input>
        </el-form-item>
        <el-button type="primary" style="width: 100%;" @click="login" :loading="loginLoading">登录</el-button>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import LoginService from './LoginService'
import { MsgAlert } from '../utils/Alert.js'

export default {
  name: 'Login',
  data () {
    return {
      loginLoading: false,
      userForm: {
        username: 'admin',
        password: 'admin123'
      },
      rules: { // 表单校验规则
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'change' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 18, message: '长度在 6 到 18 个字符', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    login () {
      // 登录前表单校验
      this.$refs.userForm.validate((valid) => {
        // 校验通过时
        if (valid) {
          // 开启按钮加载
          this.loginLoading = true
          // 调用登录服务
          LoginService.requestLogin().then(() => { // 登录成功
            // 登录成功后进行页面跳转
            this.$router.push('/')
          }).catch(Error => { // 登录失败
            // console.log(error)
            MsgAlert.error(this, '登录失败')
          }).finally(() => { // 无论登录成功或失败，关闭按钮加载
            this.loginLoading = false
          })
        } else { // 校验不通过
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.login-container{
  /* 背景图片拉升铺满 */
  background: url('../assets/bg.jpg') no-repeat;
  background-size: cover;
  overflow: hidden;
  /* 登录表单垂直居中 */
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* 登录卡片样式 */
  .box-card{
    width: 450px;
    height: 300px;
    .login-title{
      color: #666666;
    }
  }
}
</style>
