let LoginService = {
  /**
   * 请求登录
   * @returns {Promise<unknown>}
   */
  requestLogin () {
    /**
     *  promise格式：new Promise((resolve, reject) => {})
     *  resolve和reject分别为回调函数
     */
    return new Promise((resolve, reject) => {
      // 这里的本意是请求后端接口，现在使用随机数的方式模拟是否登录成功
      const m = Math.random() * 10
      // alert(m)
      setTimeout(() => {
        // 当随机数大于5时表示登录成功，否则登录失败
        if (m >= 5) {
          return resolve()
        }
        return reject(new Error('登录失败'))
      }, 300)
    })
  }
}

export default LoginService
