import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '../layout/'
import Login from '../login'
// 引入路由模块
import authModules from './modules/auth'

Vue.use(VueRouter)
/**
 * 路由划分为多模块
 * @type {*[]}
 */
const routes = [
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/',
    name: 'Layout',
    component: Layout
  },
  // 抽取到权限路由模块
  authModules,
  {
    path: '/map',
    component: Layout,
    children: [{
      path: 'index',
      name: 'Map',
      component: () => import('../views/map/')
    }]
  },
  {
    path: '/formtool',
    component: Layout,
    children: [
      {
        path: 'formdesign',
        name: 'FormDesign',
        component: () => import('../views/formtool/formdesign/')
      },
      {
        path: 'formgenerator',
        name: 'FormGenerator',
        component: () => import('../views/formtool/formgenerator/')
      }
    ]
  },
  {
    path: '*',
    name: 'Page404',
    component: () => import('../views/errorpage/404.vue')
  }
]
const router = new VueRouter({
  routes
})

export default router
