<template>
  <div class="home-container">
      <Header></Header>
      <el-container class="body-container">
        <!--  pc端菜单      -->
        <web-menu v-show="showPcMenu"></web-menu>
        <!--  app端菜单      -->
        <app-menu v-show="!showPcMenu"></app-menu>
        <el-container>
          <el-scrollbar style="width: 100%;height: 100%;">
            <BreadCrumb :titles="breadCrumb"></BreadCrumb>
            <el-main>
              <div class="content">
                <router-view></router-view>
              </div>
            </el-main>
            <el-backtop target=".el-main" :bottom="100"></el-backtop>
          </el-scrollbar>
        </el-container>
      </el-container>
  </div>
</template>

<script>
import Header from './header/'
import WebMenu from './aside/WebMenu'
import AppMenu from './aside/AppMenu'
import BreadCrumb from './breadcrumb/'
import { mapGetters } from 'vuex'

export default {
  name: 'Layout',
  components: {
    Header,
    WebMenu,
    AppMenu,
    BreadCrumb
  },
  computed: {
    ...mapGetters('ui', {
      breadCrumb: 'getBreadcrumbArr', // 获取面包屑数组，映射到this.$store.getters['ui/getBreadcrumbArr']
      asideShow: 'getAsideShow' // 是否显示侧边栏菜单，针对于不同尺寸大小的pc端浏览器，映射到this.$store.getters['ui/getAsideShow']
    }),
    /**
     * 说明：
     *  原本web-menu和app-menu是通过v-if="asideShow"来实现，但是在移动端加载页面时，
     *  发现并未app-menu组件并未初始化，但是无法初始化跳转的默认路由
     *  因此使用v-show的方式，当前组件加载时，同时加载app-menu组件完成相关初始化
     *  由于不支持v-show="!asideShow"，因此定义一个计算属性，先接收asideShow，再完成其他逻辑
     * @returns {string|boolean|*}
     */
    showPcMenu () { // 是否显示pc菜单，是则显示，否则显示app菜单
      return this.asideShow
    }
  }
}
</script>

<style lang="scss" scoped>
.home-container {
  width: 100%;
  /* 高度原本是100%，但由于头部有一点距离，因此会产生滚动，避免滚动，减去头部所占的高度60px*/
  height: calc(100% - 60px);
  position: relative;
  .body-container{
    height: 100%;
    width: 100%;
    /* 头部做了固定，因此减去头部所占的高度60px，即下移60px */
    position: absolute;
    top: 60px;
    .el-main{
      width: 100%;
      background-color: #f1f2f5;
      padding: 10px !important;
      .content{
        background-color: #fff;
        padding:20px;
        /* 当面包屑组件使用第二种方式时，开启 */
        /*margin: 68px 18px 20px;*/
      }
    }
  }
}
</style>
