// state
const state = {
  permissions: [] // 权限菜单，菜单对象

}
// getters
const getters = {
  getPermissions (state) {
    return state.permissions
  }

}
// mutations
const mutations = {
  ADD_PERMISSIONS (state, permissions) {
    state.permissions = [...permissions]
  }
}
// actions
const actions = {
  addPermissions (context, permissions) { // 添加权限
    context.commit('ADD_PERMISSIONS', permissions)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
