/**
 * JS工具类
 */
export default class JSUtil {
  /**
   * 判断当前设备是移动端还是pc端
   * @returns {boolean} 是则移动端，否则pc端
   */
  static isFromDevice () {
    return /Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)
  }
  /**
   * 获取网页可见区域宽
   * @param width
   * @returns {number}
   */
  static getScreenWidth () {
    return document.body.offsetWidth
  }
}
