import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './css/index.scss'
// 全局注册element
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
// 注册全局过滤器
import * as dateFilters from './filters/DateFilters.js'
// FormMaking
import FormMaking from 'form-making'
import 'form-making/dist/FormMaking.css'

Vue.config.productionTip = false
Vue.use(ElementUI)

// 全局注册百度地图
// import BaiduMap from 'vue-baidu-map'// 全局引入百度地图
// Vue.use(BaiduMap, {
//   // ak 是在百度地图开发者平台申请的密钥 详见 http://lbsyun.baidu.com/apiconsole/key */
//   ak: 'PDpRVoNGsggN4k5kg4C8Gywc7KFEzvZ4'
// })

// Object.keys是es5中新增的方法，用来获取对象自身所有的可枚举的属性名，
// 但不包括原型中的属性，然后返回一个由属性名组成的数组。
// 全局注册过滤器：Vue.filter(过滤器名称,function(input){})
Object.keys(dateFilters).forEach(key => {
  Vue.filter(key, dateFilters[key])
})

Vue.use(FormMaking)
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
