<template>
  <el-header>
    <div class="header-left">
      <span :class="titleStyle" > {{ title }}</span>
      <span v-show="toggleActive" class="toggle-btn" @click="handlerToggle">
        <i v-if="collapseState" class="el-icon-s-unfold"></i>
        <i v-else class="el-icon-s-fold"></i>
      </span>
    </div>
    <el-dropdown class="header-right">
      <div class="active-zone">
        <el-avatar class="avatar" :size="40" :src="avatar"></el-avatar>
        <span class="username">{{username}}</span>
      </div>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item><div @click="logout">退出</div></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </el-header>
</template>

<script>
import SystemSetting from '../../SystemSetting'
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'Header',
  created () {
  },
  data () {
    return {
      username: SystemSetting.username, // 用户名
      avatar: require('../../assets/avatar.png'), // 头像
      showTitle: {
        unfold: SystemSetting.unfoldTitle, // 折叠前的标题
        fold: SystemSetting.foldTitle // 折叠后的标题
      },
      logoutRoute: SystemSetting.logoutRoute // 退出后跳转的路由
    }
  },
  computed: {
    ...mapGetters('ui', {
      collapseState: 'getCollapse', // 折叠状态，false为未折叠，true为折叠，映射到this.$store.getters['ui/getCollapse']
      toggleActive: 'getToggleActive' // toggle是否可用,映射到this.$store.getters['ui/getToggleActive']
    }),
    title () { // 标题：当折叠状态为true并且toggle可用时，才显示折叠的标题
      return this.collapseState && this.toggleActive ? this.showTitle.fold : this.showTitle.unfold
    },
    titleStyle () { // 根据collapse状态添加标题样式：当折叠状态为true并且toggle可用时，才显示折叠的样式
      // foldTitle和unfoldTitle定义在下面的css样式中
      return this.collapseState && this.toggleActive ? 'foldTitle' : 'unfoldTitle'
    }
  },
  methods: {
    ...mapActions('ui', {
      collapse: 'toggleCollapse'// 更改折叠状态，映射到this.$store.dispatch('ui/toggleCollapse')
    }),
    /* 处理toggle事件 */
    handlerToggle () {
      this.collapse() // 更改折叠状态
    },
    logout () {
      // 清除用户信息
      // CommonUtil.removeUserInfo()
      // 跳转到登录页
      this.$router.push(this.logoutRoute)
    }
  }
}
</script>

