  line-height: 60px;
  display: flex;
  justify-content: space-between;
  /*padding: 0 20px;*/
  /* 固定头部 */
  width: 100%;
  position: fixed;
  z-index: 666;
  /* 左边的样式 */
  .header-left{
    display:flex;
    justify-content: center;
    /* 未折叠侧边栏菜单前的样式 */
    .unfoldTitle{
      font-size: 20px;
      font-weight: bolder;
      text-align: left;
      display: inline-block;
      /* 原本和aside的宽度一样260px，但由于占了左边20px的padding，因此减去20px*/
      width: 240px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    /* 折叠侧边栏菜单后的样式 */
    .foldTitle{
      font-size: 20px;
      font-weight: bolder;
      text-align: left;
      display: inline-block;
      /* 原本和aside的宽度一样65px，但由于占了左边20px的padding，因此减去20px*/
      width: 45px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    /* toggle按钮 */
    .toggle-btn{
      display: inline-block;
      width: 50px;
      /* 取至背景色透明度的0.8 */
      background-color: #78b9ff;
      cursor: pointer;
    }
  }
  /* 右边的样式 */
  .header-right{
    cursor: default;
    .active-zone{
      padding: 0 18px;
      display: flex;
      align-items: center;
      .avatar{
        margin-right: 10px;
      }
      .username{
        font-size: 16px;
        color: #ffffff;
      }
    }
    .active-zone:hover{
      /* 取至背景色透明度的0.8 */
      background-color: #78b9ff;
    }
  }
}
</style>
