/**
 * 权限服务
 * @type {{requestGetPermission(): Promise<unknown>}}
 */
let PermissionService = {
  /**
   * 请求获取权限
   * @returns {Promise<unknown>}
   */
  requestGetPermission () {
    return new Promise((resolve, reject) => {
      // 调用后端接口获取数据，此处使用静态数据模拟
      // 请求获取权限
      const permissions = transfer2Permissions()
      resolve(permissions)
    })
  }
}

