<template>
  <div id="app">
    <router-view/>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import JSUtil from './utils/JSUtil'
import SystemSetting from './SystemSetting'

export default {
  name: 'APP',
  mounted () {
    // 1.页面挂载时布局适配
    this.layoutAdaptation(JSUtil.getScreenWidth())
    let _this = this
    // 2.动态调整可见屏幕宽度时布局适配
    window.onresize = function () { // 定义窗口大小变更通知事件
      _this.layoutAdaptation(JSUtil.getScreenWidth())
    }
  },
  computed: {
    ...mapGetters('ui', [
      // 映射到this.$store.getters['ui/getCurrentScreenWidth']
      'getCurrentScreenWidth',
      // 折叠状态，false为未折叠，true为折叠，映射到this.$store.getters['ui/getCollapse']
      'getCollapse',
      // 是否显示侧边栏菜单，针对于不同尺寸大小的pc端浏览器，映射到this.$store.getters['ui/getAsideShow']
      'getAsideShow',
      // toggle是否可用,映射到this.$store.getters['ui/getToggleActive']
      'getToggleActive'
    ])
  },
  methods: {
    ...mapActions('ui', [
      // 映射到this.$store.dispatch['ui/updateCurrentScreenWidth']
      'updateCurrentScreenWidth',
      // 映射到this.$store.dispatch['ui/toggleCollapse']
      'toggleCollapse',
      // 映射到this.$store.dispatch['ui/updateAsideShow']
      'updateAsideShow',
      // 映射到this.$store.dispatch['ui/updateToggleActive']
      'updateToggleActive'
    ]),
    /**
     * 布局适配
     * 说明：
     * 默认：getToggleActive为true，getCollapse为false，getAsideShow为true
     * 屏幕宽度 在684和1120之间：getToggleActive为false，getCollapse为true，getAsideShow为true
     * 屏幕宽度 小于684 ：getToggleActive为true，getCollapse为true，getAsideShow为false
     * @param width
     */
    layoutAdaptation (width) {
      if (JSUtil.isFromDevice()) {
        console.log('来自移动端')
      } else {
        console.log('来自pc端')
      }
      console.log('当前屏幕宽度：', width)
      // alert(width)
      /* 1.更新当前屏幕宽度 */
      this.updateCurrentScreenWidth(width)
      // console.log('可见屏幕宽度：', width)
      /* 2.对不同屏幕大小进行布局适配 */
      // 当屏幕宽度大于648且小于1120时，默认收起菜单，并且toggle不可点击
      if (width > SystemSetting.MAX_APP_SCREEN_WIDTH && width < SystemSetting.MIN_PC_SCREEN_WIDTH) {
        // 如果为未折叠，则更改为折叠
        if (!this.getCollapse) {
          this.toggleCollapse()
        }
        // 如果toggle可用，则更改为不可用
        if (this.getToggleActive) {
          this.updateToggleActive()
        }
        // 如果侧边栏菜单隐藏，在显示侧边栏菜单
        if (!this.getAsideShow) {
          this.updateAsideShow()
        }
        return
      } else if (width < SystemSetting.MAX_APP_SCREEN_WIDTH) { // 当屏幕小于648px时，隐藏侧边栏菜单，并使用app菜单
        // 如果未折叠，则更改为折叠
        if (!this.getCollapse) {
          this.toggleCollapse()
        }
        // 如果toggle不可用，则更改为可用
        if (!this.getToggleActive) {
          this.updateToggleActive()
        }
        // 如果侧边栏菜单显示，则隐藏侧边栏菜单
        if (this.getAsideShow) {
          this.updateAsideShow()
        }
        return
      }
      // 恢复折叠状态为未折叠
      if (this.getCollapse) {
        this.toggleCollapse()
      }
      // 恢复toggle可用
      if (!this.getToggleActive) {
        this.updateToggleActive()
      }
      // 恢复显示侧边栏菜单
      if (!this.getAsideShow) {
        this.updateAsideShow()
      }
    }
  }
}
</script>

<style lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
</style>
