<template>
  <el-aside :width="asideWidth">
    <el-menu
      :default-openeds="defaultOpenIds"
      :default-active="defaultActive"
      :unique-opened=true
      :collapse="collapse"
      :collapse-transition="false"
      @select="handleSelect"
      v-for="(menu,index) in menuList"
      :key="index">
      <el-submenu v-if="menu.children && menu.children.length > 0" :index="menu.menuId">
        <template slot="title">
          <i :class="menu.icon"></i>
          <span>{{menu.menuName}}</span>
        </template>
        <el-menu-item-group>
          <router-link v-for="(child,c_index) in menu.children" :key="c_index" :to = "child.route" tag="span">
            <el-menu-item :index="child.menuId" >{{child.menuName}}</el-menu-item>
          </router-link>
        </el-menu-item-group>
      </el-submenu>
      <router-link v-else :to = "menu.route" tag="span">
        <el-menu-item :index="menu.menuId">
          <i :class="menu.icon"></i>
          <span slot="title">{{ menu.menuName }}</span>
        </el-menu-item>
      </router-link>
    </el-menu>
  </el-aside>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import PermissionService from '../PermissionService.js'
import SystemSetting from '../../SystemSetting'

export default {
  name: 'Aside',
  created () {
  },
  mounted () {
    // 获取菜单权限
    PermissionService.requestGetPermission().then(permissions => {
      // 1. 复制到当前菜单列表
      this.menuList = [...permissions]
      // 2. 存储到state
      this.addPermissions(permissions)
      // 3. 页面的默认设置
      this.defaultSetting()
    })
  },
  computed: {
    // collapse () { // 获取collapse状态
    //   return this.$store.getters['ui/getCollapse']
    // },
    ...mapGetters('ui', {
      collapse: 'getCollapse', // 映射到 return this.$store.getters['ui/getCollapse']
      currentScreenWidth: 'getCurrentScreenWidth' // 映射到return this.$store.getters['ui/getCurrentScreenWidth']
    }),
    asideWidth () { // 侧边菜单栏缩放时的宽度
      return this.collapse ? '65px' : '260px'
    }
  },
  data () {
    return {
      menuList: [],
      defaultActive: '', // 当前激活的索引
      defaultOpenIds: [] // 当前打开的子菜单数组,本系统只显示打开一个菜单
    }
  },
  methods: {
    // /* 设置面包屑 */
    // setBreadCrumb (breadCrumb) {
    //   this.$store.dispatch('ui/updateBreadcrumb', breadCrumb)
    // },
    // /* 设置默认路由 */
    // setDefaultRoute (route) {
    //   this.$store.dispatch('ui/updateDefaultRoute', route)
    // },
    // /* 添加权限 */
    // addPermissions (permissions) {
    //   this.$store.dispatch('auth/addPermissions', permissions)
    // },
    ...mapActions('ui', {
      setBreadCrumb: 'updateBreadcrumb', // 映射到 this.$store.dispatch('ui/updateBreadcrumb', breadCrumb)
      setDefaultRoute: 'updateDefaultRoute', // 映射到this.$store.dispatch('ui/updateDefaultRoute', route)
      toggle: 'toggleCollapse'// 更改折叠状态，映射到this.$store.dispatch('ui/toggleCollapse')
    }),
    ...mapActions('auth', {
      addPermissions: 'addPermissions'// 映射到this.$store.dispatch('auth/addPermissions', permissions)
    }),
    /*  菜单激活回调 */
    handleSelect (index, indexPath) {
      // 判断当前屏幕宽度是否小于684(也就是移动端)，如果是，则点击菜单后，自动关闭抽屉（菜单）
      if (this.currentScreenWidth < SystemSetting.MAX_APP_SCREEN_WIDTH) {
        this.toggle()
      }
      // console.log('点击的菜单索引：', index, indexPath)
      let arr = [] // 面包屑数组
      let activeId // 当前激活的索引
      // 获取点击的一级菜单
      const menu = this.menuList.find(item => item.menuId === indexPath[0])
      if (menu) {
        arr.push(menu.menuName)
        activeId = menu.menuId
        // 获取点击的二级菜单
        const children = menu.children
        if (children && children.length > 0) {
          const child = children.find(item => item.menuId === indexPath[1])
          arr.push(child.menuName)
          activeId = child.menuId
        }
      }
      // 1.设置当前面包屑
      this.setBreadCrumb(arr)
      // 2.去除默认激活的菜单
      //    原本逻辑上置空即可，但会导致第一次点击有问题，因此另外做了处理，把当前激活索引作为默认激活的索引
      this.defaultActive = activeId
      // 3.修改默认开启的子菜单：本系统只显示打开一个菜单，替换上一次的标识
      this.defaultOpenIds.splice(0, 1, menu.menuId)
    },
    /* 默认设置：面包屑、当前激活的菜单索引，当前打开子菜单的索引，默认跳转的路由 */
    defaultSetting () {
      // 获取第一个菜单
      const menu0 = this.menuList[0]
      if (menu0) {
        const arr = [] // 面包屑
        let defaultRoute // 默认跳转的路由
        arr.push(menu0.menuName)
        defaultRoute = menu0.route
        // 获取第一个菜单的第一个子菜单
        const child0 = menu0.children[0]
        if (child0) {
          arr.push(child0.menuName)
          defaultRoute = child0.route
        }
        // 1.设置默认的面包屑：第一个菜单
        this.setBreadCrumb(arr)
        // 2.设置当前激活的菜单索引
        this.defaultActive = child0 ? child0.menuId : menu0.menuId
        // 3.设置当前打开子菜单的索引
        this.defaultOpenIds.push(menu0.menuId)
        // 4.设置默认跳转的路由
        this.setDefaultRoute(defaultRoute)
        // console.log('默认跳转的路由：', defaultRoute)
        // 5.跳转到默认路由
        // 获取当前路由，判断是否与默认路由一样，如果不一样，则跳转
        // 假如不做这个判断，则会报跳转到重复路由的错误
        // console.log(this.$route.path)
        if (this.$route.path !== defaultRoute) {
          this.$router.push(defaultRoute)
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.el-aside{
  height: 100%;
  /*border-right: 1px solid #eee; !* 自定义右边框 *!*/
  .el-menu{
    /* 设置侧边栏菜单最小高度100%，由于选择生成多个el-menu方案，因此不采用，并且禁用默认的右边框 */
    /*min-height: 100%;*/
    border-right: none !important; /* 禁用默认的右边框，使用自定义的右边框 */
    background-color: #ffffff;
    border-right: solid 1px #eee;
    text-align: left;
    /*height: 100%;*/
    /* 菜单激活背景色*/
    .el-menu-item.is-active{
      background-color: #f0f8ff;
    }
  }
}
</style>
